const map = () => {
  
ymaps.ready(function () {
  var myMap = new ymaps.Map('map', {
      center: [55.86314863354213,37.605452838623044],
      zoom: 10
  }, {
      searchControlProvider: 'yandex#search'
  }),

  //   .
  MyIconContentLayout = ymaps.templateLayoutFactory.createClass(
      '<div style="color: #FFFFFF; font-weight: bold;">$[properties.iconContent]</div>'
  ),

  myPlacemark = new ymaps.Placemark(myMap.getCenter(), {
      hintContent: ',  .&nbsp;. , 20.  &ldquo; &rdquo;',
      balloonContent: ',  .&nbsp;. , 20.  &ldquo; &rdquo;'
  }, {
      // .
      //     .
      iconLayout: 'default#image',
      //    .
      iconImageHref: 'images/map__icon.png',
      //  .
      iconImageSize: [41, 53],
      //      
      //  "" ( ).
      iconImageOffset: [-5, -38]
  }),
  myPlacemarkWithContent = new ymaps.Placemark([55.702520357737455,37.944722940475465], {
      hintContent: ',  .&nbsp;. , 3.  &ldquo; &rdquo;',
      balloonContent: ',  .&nbsp;. , 3.  &ldquo; &rdquo;',
  }, {
      iconLayout: 'default#imageWithContent',
      iconImageHref: 'images/map__icon.png',
      iconImageSize: [41, 53],
      iconImageOffset: [-5, -38],
      iconContentLayout: MyIconContentLayout
  }),
  myPlacemarkWithContent2 = new ymaps.Placemark([55.72575218747117,37.743419424244166], {
      hintContent: ',  .&nbsp;. 2-  , 422.  &ldquo; &rdquo;',
      balloonContent: ',  .&nbsp;. 2-  , 422.  &ldquo; &rdquo;',
  }, {
      iconLayout: 'default#imageWithContent',
      iconImageHref: 'images/map__icon.png',
      iconImageSize: [41, 53],
      iconImageOffset: [-5, -38],
      iconContentLayout: MyIconContentLayout
  });

  myMap.geoObjects
    .add(myPlacemark)
    .add(myPlacemarkWithContent)
    .add(myPlacemarkWithContent2);
});

}

export default map;